/* resume.css -- Styles for specific portions of the resume page 

Sections
-----
  A - Resume Content
  B - Buttons
    i  - PDF button
    ii - DOCX button
  C - Print Settings
-----
*/

/* A - Resume Content */
#resumeContent {
  padding: 2em;
  box-shadow: 0 0 10px rgba(0,0,0, 0.4);
  background-color: whitesmoke;
}

.resumeSection {
  margin-left: 1vh;
  margin-right: 1vh;
  margin-bottom: 1em;
}

.resumeSection a {
  font-weight: normal;
  color: black;
  text-decoration: underline;
}

.resumeSection a.link {
  font-style: italic;
}

.resumeSection span {
  font-weight: bold;
}

.resumeSection span.date {
  float: right;
}

.resumeSection span.italics {
  font-weight: normal;
  font-style: italic;
}

.resumeSection h1 {
  font-family: 'Crimson Text', Georgia, 'Times New Roman', Times, serif;
  font-size: 2em;
  color: rgb(74, 36, 58);
}

.resumeSection h2 {
  font-family: 'Crimson Text', Georgia, 'Times New Roman', Times, serif;
  font-size: 1.5em;
  color: rgb(74, 36, 58);
}

.resumeSection ul li{
  list-style-type: none;
  margin-left: 1em;
}

.resumeSection ul li ul {
  margin-bottom: 1em;
}

.resumeSection ul li ul li {
  list-style-type: initial;
  margin-left: 2em;
}
/* Section A End */

/* B - Buttons */
#resumeButtons {
  text-align: center;
  margin-top: 1em;
  margin-bottom: 2em;
}

#resumeButtons li {
  display: inline;
  list-style-type: none;
  padding: 0 1%;
}

#resumeButtons button {
  font-family: 'Source Sans Pro', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-weight: bold;
  width: 40%;
  font-size: 0.9em;
  text-decoration: none;
  color: whitesmoke;
  border: none;
  padding: 0.75em;
}

/* i - 'Download as PDF' button */
#resumeButtons button.pdf {
  background-color: rgb(199,9,9);
}

#resumeButtons button.pdf:hover {
  background-color: rgb(209, 29, 29);
  box-shadow: 0 0 15px rgba(229, 39, 39, 0.25);
}
/* Subsection i End */

/* ii - 'Download as DOCX' button */
#resumeButtons button.docx {
  background-color: rgb(41,83,145);
}

#resumeButtons button.docx:hover {
  background-color: rgb(51, 93, 155);
  box-shadow: 0 0 15px rgba(71, 113, 175, 0.25);
}
/* Subsection ii End */
/* Section B End */

/* Section C - Print Settings */
@media print {
  @page {
    size: letter portrait;
    marks: none;
    bleed: 1in;
  }

  * {
    background-color: white;
    position: static !important;
  }

  .screenDivider {
    display: inline;
  }

  .screenBreak {
    display: none;
  }

  .navMenu, .banner, #resumeButtons, footer {
    display: none;
  }

  #content, #resumeContent {
    margin: 0;
    padding: 0;
  }

  #resumeContent {
    background-color: white;
    padding: 0;
    margin: 0;
    box-shadow: none;
  }
}
/* Section C End */

@media screen and (max-width: 600px){
  .screenDivider {
    display: none;
  }
  .screenBreak {
    display: inline;
  }

  .resumeSection span.date {
    float: left;
  }

  span {
    margin-bottom: 0 !important;
  }
}

@media screen and (min-width: 601px){
  .screenDivider {
    display: inline;
  }

  .screenBreak {
    display: none;
  }

  .resumeSection span.date {
    float: right;
  }
}